import Home from '@/components/HomePage.vue'
import Register from '@/components/RegisterPage.vue'
import Submission from '@/components/SubmissionPage.vue'
import FAQ from '@/components/FaqPage.vue'
import Baseline from '@/components/BaselinePage.vue'
import Demo from '@/components/DemoPage.vue'
import Download from '@/components/DownloadPage.vue'
import Workshop from '@/components/WorkshopPage.vue'
import { createRouter, createWebHistory } from 'vue-router';

 
const routers = [
    {
      path: '/',
      name: 'HomePageVue',
      component: Home
    },
    {
      path: '/register',
      name: 'RegisterPage',
      component: Register
    },
    {
      path: '/submission',
      name: 'SubmissionPage',
      component: Submission
    },
    {
      path: '/faqs',
      name: 'FaqPage',
      component: FAQ
    },
    {
      path: '/baseline',
      name: 'BaselinePage',
      component: Baseline,
    },
    {
      path: '/demo',
      name: 'DemoPage',
      component: Demo,
    },
    {
      path: '/download',
      name: 'DownloadPage',
      component: Download
    },
    {
      path: '/workshop',
      name: 'WorkshopPage',
      component: Workshop
    }
]

const router = createRouter({
  history: createWebHistory(),
  routes: routers, // `routes: routes` 
})

export default router;

